import Stage from './stage'
import Map, { Step } from './map'
import Game, { EventMap } from './game'
// import {...} from './index.d'

export enum Orientation {
    Right,  // 0 
    Down,   // 1 
    Left,   // 2 
    Up      // 3 
}

export enum ActorType {
    General,    // 0 ()
    Player,     // 1 
    NPC,        // 2 Non-Player Character 
    Enemy       // 3 
}

export enum ActorStatus {
    Silent, // 0 /
    Normal, // 1 
    Pause,  // 2 
    Timing, // 3 
    Error,  // 4 
    Dead
}

// offset 
export interface Coord {
    x: number
    y: number
    offset?: number
}

export interface ControlCache {
    orientation?: number
}

export default class Actor {
    params: Partial<Actor>
    stage: Stage
    game: Game
    canvas: HTMLCanvasElement
    id: number = 0
    x: number = 0
    y: number = 0
    width: number = 20
    height: number = 20
    type = ActorType.General
    color: string = '#F00'
    status = ActorStatus.Normal
    orientation = Orientation.Right
    speed: number = 0
    events: EventMap = {}

    location: Map | undefined
    coord: Coord = { x: 0, y: 0 }
    vector: Step = { x: 0, y: 0 }
    path: Step[] = []

    frames: number = 1   // times 
    times: number = 0    //
    timeout: number = 0  //()
    control: ControlCache = {} //,
    update: () => void = () => { }
    draw: (context: CanvasRenderingContext2D) => void = () => { }

    constructor(game: Game, stage: Stage, params: Partial<Actor>) {
        this.params = params || {};
        this.canvas = game.canvas;
        this.events = game.events;
        this.stage = stage;
        this.game = game;
        Object.assign(this, this.params);
    }
}
